const txtIdElm = $("#txtId");
const txtNameElm = $("#txtName");
const txtAddressElm = $("#txtAddress");
const txtContactElm = $("#txtContact");
const btnSave = $("#btn-save-customer");
const REST_API_URL = "http://localhost:8080/pos";

const customerDetails =[txtNameElm,txtAddressElm,txtContactElm];

customerDetails.forEach(txt=>txt.addClass("animate__animated"));




btnSave.on('click',()=>{
    if(!isValid())return;
    const customerJson = JSON.stringify({
        name: txtNameElm.val().trim(),
        address: txtAddressElm.val().trim(),
        contact: txtContactElm.val().trim()
    });
    const jqxhr = $.ajax(`${REST_API_URL}/customers`,{
        method:'POST',
        contentType:'application/json',
        data: customerJson
    });
    jqxhr.done(()=>{
        resetForm("clearData");
    })
    jqxhr.fail(()=>{})
});



function isValid(){
    const name = txtNameElm.val().trim();
    const address = txtAddressElm.val().trim();
    const contact = txtContactElm.val().trim();
    let validity = true;
    resetForm();

    setTimeout(()=>{
        if (!/^.{3,}$/.test(address)){
            txtAddressElm.addClass("animate__shakeX is-invalid");
            txtAddressElm.select();
            txtAddressElm.focus();
            validity = false;
        }
    },0);
    setTimeout(()=>{
        if (!/^\d{3}-\d{7}$/.test(contact)){
            txtContactElm.addClass("animate__shakeX is-invalid");
            txtContactElm.select();
            txtContactElm.focus();
            validity = false;
        }
    },0);
    setTimeout(()=>{
        if (!/^[A-Za-z ]+$/.test(name)){
            txtNameElm.addClass("animate__shakeX is-invalid");
            txtNameElm.select();
            txtNameElm.focus();
            validity = false;
        }
    },0);

    return validity;

}
function resetForm(clearData){
    customerDetails.forEach(txt=> {
        txt.removeClass("animate__shakeX is-invalid");
        if(clearData) {
            txt.val('');

        }
    })
    if (clearData)txtNameElm.focus();

}